.meeting-note-form-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.meeting-note-form-container {
  background: white;
  border-radius: 8px;
  padding: 2rem;
  width: 90%;
  max-width: 700px;
  max-height: 90vh;
  overflow-y: auto;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.meeting-note-form-container h2 {
  margin-top: 0;
  margin-bottom: 1.5rem;
  color: #333;
}

.form-group {
  margin-bottom: 1.5rem;
}

.form-group label {
  display: block;
  margin-bottom: 0.5rem;
  font-weight: 500;
  color: #555;
}

.form-group input,
.form-group textarea {
  width: 100%;
  padding: 0.75rem;
  border: 1px solid #ddd;
  border-radius: 4px;
  font-size: 1rem;
  font-family: inherit;
  box-sizing: border-box;
}

.form-group textarea {
  resize: vertical;
  min-height: 200px;
}

.ckeditor-wrapper {
  border: 1px solid #ddd;
  border-radius: 4px;
  overflow: hidden;
}

.ckeditor-wrapper .ck-editor {
  min-height: 300px;
}

.ckeditor-wrapper .ck-editor__editable {
  min-height: 300px;
}

.ckeditor-wrapper .ck-editor__editable:focus {
  border-color: var(--primary-500);
  box-shadow: 0 0 0 3px rgba(0, 99, 124, 0.1);
}

/* Ensure lists display correctly in CKEditor */
.ckeditor-wrapper .ck-editor__editable ul,
.ckeditor-wrapper .ck-editor__editable ol {
  margin: 1rem 0;
  padding-left: 2rem;
  list-style-position: outside;
}

.ckeditor-wrapper .ck-editor__editable ul {
  list-style-type: disc;
}

.ckeditor-wrapper .ck-editor__editable ol {
  list-style-type: decimal;
}

.ckeditor-wrapper .ck-editor__editable li {
  margin: 0.5rem 0;
  display: list-item;
}

.ckeditor-wrapper .ck-editor__editable ul ul {
  list-style-type: circle;
}

.ckeditor-wrapper .ck-editor__editable ul ul ul {
  list-style-type: square;
}

.ckeditor-wrapper .ck-editor__editable ol ol {
  list-style-type: lower-alpha;
}

.ckeditor-wrapper .ck-editor__editable ol ol ol {
  list-style-type: lower-roman;
}

.form-group input:focus,
.form-group textarea:focus {
  outline: none;
  border-color: var(--primary-500);
  box-shadow: 0 0 0 3px rgba(0, 99, 124, 0.1);
}

.form-actions {
  display: flex;
  gap: 1rem;
  justify-content: flex-end;
  margin-top: 2rem;
}

.btn {
  padding: 0.75rem 1.5rem;
  border: none;
  border-radius: 4px;
  font-size: 1rem;
  font-weight: 500;
  cursor: pointer;
  transition: background-color 0.2s;
}

.btn-primary {
  background-color: var(--primary-500);
  color: white;
}

.btn-primary:hover {
  background-color: var(--primary-600);
}

.btn-secondary {
  background-color: #6c757d;
  color: white;
}

.btn-secondary:hover {
  background-color: #5a6268;
  color: white;
}

